.glider,
.glider-contain {
    margin: 0 auto;
    position: relative
}

.glider,
.glider-track {
    transform: translateZ(0)
}

.glider-dot,
.glider-next,
.glider-prev {
    border: 0;
    padding: 0;
    user-select: none;
    outline: 0
}

.glider-contain {
    width: 100%
}

.glider {
    overflow-y: hidden;
    -webkit-overflow-scrolling: touch;
    -ms-overflow-style: none
}

.glider-track {
    width: 100%;
    margin: 0;
    padding: 0;
    display: flex;
    z-index: 1
}

.glider.draggable {
    user-select: none;
    cursor: grab
}
.glider.draggable .glider-slide img {
    user-select: none;
    pointer-events: none
}
.glider.drag {
    cursor: grabbing
}
.glider-slide {
    user-select: none;
    justify-content: center;
    align-content: center;
    width: 100%;
    min-width: 150px
}
.glider-slide img {
    max-width: 100%
}
.glider::-webkit-scrollbar {
opacity: 0;
height: 0
}
.glider-next,
.glider-prev {
    position: absolute;
    background: 0 0;
    z-index: 2;
    font-size: 1.2rem;
    font-weight: 100;
    text-decoration: none;
    left:-0.5rem;
    top: 50%;
    transform: translateY(-50%);
    padding:1rem;
    cursor: pointer;
    color: $dark-gray;
    opacity: 1;
    border-radius: 50%;
    line-height: 1;
    transition: opacity .5s cubic-bezier(.17, .67, .83, .67), color .5s cubic-bezier(.17, .67, .83, .67), .5s cubic-bezier(.17, .67, .83, .67);
}

.glider-next:focus,
.glider-next:hover,
.glider-prev:focus,
.glider-prev:hover {
color: $primary-clr;
background-color: rgba($dark-gray, 0.2);
}

.glider-next {
right: -0.5rem;
left: auto
}

.glider-next.disabled,
.glider-prev.disabled {
opacity: .25;
color: #666;
cursor: default
}
.glider-hide {
opacity: 0
}
.glider-dots {
user-select: none;
display: flex;
flex-wrap: wrap;
justify-content: center;
margin: 1rem auto 0;
padding: 0
}

.glider-dot {
display: block;
cursor: pointer;
color: #ccc;
border-radius: 20px;
background: $secondary-clr;
width: 14px;
height: 14px;
margin: 7px
}
.glider-dot:focus,
.glider-dot:hover {
background: $primary-clr;
}
.glider-dot.active {
background: $primary-clr;
border-color: darken($primary-clr, 30%);
}

@media(max-width:36em) {
.glider::-webkit-scrollbar {
opacity: 1;
-webkit-appearance: none;
width: 7px;
height: 3px
}
.glider::-webkit-scrollbar-thumb {
opacity: 1;
border-radius: 99px;
background-color: rgba(156, 156, 156, .25);
-webkit-box-shadow: 0 0 1px rgba(255, 255, 255, .25);
box-shadow: 0 0 1px rgba(255, 255, 255, .25)
}
}